import React, { useState, useEffect, useCallback } from 'react';
import { MyProfileBlock } from './styles';
import ApiDefault from '../utils';

const useForceUpdate = () => useState()[1];

const MyProfile = ({ state, actions }) => {
    const [myInfo, setMyInfo] = useState({});
    useEffect(() => {
        ApiDefault.get('user', {
            headers: {
                Authorization: state.accessToken
            }
        }).then(res => {
            setMyInfo(res.data);
        }).catch(e => {});
    }, []);
    return (
        <MyProfileBlock>
            <h3><pre>{String(myInfo.userNumber)[0]} {String(myInfo.userNumber)[1]} {myInfo.userName}</pre></h3>
            <section>
                <article>
                    <span></span>
                    <span>{myInfo.homeworkLeft}</span>
                </article>
                <article>
                    <span> </span>
                    <span>{myInfo.homeworkDone}</span>
                </article>
            </section>
        </MyProfileBlock>
    );
};

export default MyProfile;