import React, { useState, useEffect, useCallback } from 'react';
import * as S from './styles';
import blackXButton from './img/blackXButton.png';
import checkIcon from './img/checkIcon.png';
import ApiDefault from '../utils';

const SignUp = ({modalOn, setModalOn}) => {
    const [page, setPage] = useState(1);
    const [sliding, setSliding] = useState(false);
    const [isPaint, setIsPaint] = useState(false);
    const [signupInfo, setSignupInfo] = useState({
        name: "",
        studentNumber: "",
        personalCode: "",
        email: "",
        emailCode: "",
        password: "",
        chkPassword: "",
    });
    useEffect(() => {
        setTimeout(() => {
            setSliding(true);
        }, 1)
    }, []);
    const onChange = useCallback((e) => {
        setSignupInfo({
            ...signupInfo,
        [e.target.name]: e.target.value
        });
    }, [signupInfo]);
    const onSubmit = useCallback(e => {
        e.preventDefault();
        if (page === 1) {
             if (!signupInfo.name || !signupInfo.studentNumber || !signupInfo.email || !signupInfo.emailCode) return alert('    .');
            let form = new FormData();
            form.append('email', signupInfo.email);
            form.append('code', signupInfo.emailCode);
            ApiDefault.post('user/validemail', form).then(res => {
                setIsPaint(true);
                setTimeout(() => {
                    setIsPaint(false);
                    setPage(2);
                }, 1000);
            }).catch(err => {
                switch (err.response.status) {
                    case 400: 
                        alert('   .');
                        break;
                    case 401:
                        alert('  .');
                        break;
                    case 409:
                        alert('   .');
                        break;
                    default:
                        alert(' .');
                }
            })
        } else if (page === 2) {
            if (!signupInfo.personalCode || ! signupInfo.password || !signupInfo.chkPassword) return alert('    .');
            if (signupInfo.password !== signupInfo.chkPassword) return alert('  .');
            ApiDefault.post('user', {
                "userEmail": signupInfo.email,
                "userPw": signupInfo.password,
                "userName": signupInfo.name,
                "userNumber": signupInfo.studentNumber,
                "authCode": signupInfo.personalCode
            }).then(res => {
                setModalOn({ ...modalOn, signup: false });
            }).catch(err => {
                alert(' .');
            })
        }
    }, [signupInfo, page]);
    const onCheckEmail = useCallback(() => {
        let form = new FormData();
        form.append('email', signupInfo.email);
        ApiDefault.post('user/authemail', form).then(res => {
            alert('  .');
        }).catch(err => {
            switch (err.response.status) {
                case 410: {
                    alert('   .');
                    break;
                }
                default : {
                    
                }
            }
        })
    }, [signupInfo.email]);
    return (
        <S.ModalBackground>
            <S.LOGINSIGNUPWrapper sliding={sliding}>
                <S.SignUpLeftBlock>
                    <h1>WELCOME</h1>
                    <h4>SCARFS</h4>
                </S.SignUpLeftBlock>
                <S.SignUpRightBlock>
                    <img alt="" src={blackXButton}  onClick={() => setModalOn({ ...modalOn, signup: false })}/>
                    <S.MainWrapper>
                        <h1>SIGNUP</h1>
                        { 
                            page === 1 ?
                            <>
                                <section>
                                    <div>
                                        <div>
                                            <span></span>
                                            <S.LOGINSIGNUPInput placeholder="" name="name" onChange={onChange} value={signupInfo.name} />
                                        </div>
                                        <div>
                                            <span></span>
                                            <S.LOGINSIGNUPInput placeholder="1101" name="studentNumber" onChange={onChange} value={signupInfo.studentNumber} />
                                        </div>
                                    </div>
                                    <div>
                                        <S.InputWrapperWithAuthorization>
                                            <span> </span>
                                            <div>
                                                <S.LOGINSIGNUPInput placeholder="sample@dsm.hs.kr" name="email" onChange={onChange} value={signupInfo.email} />
                                                <button onClick={onCheckEmail}></button>
                                            </div>
                                        </S.InputWrapperWithAuthorization>
                                        <form onSubmit={onSubmit}>
                                            <span> </span>
                                            <S.LOGINSIGNUPInput placeholder="   ." name="emailCode" onChange={onChange} value={signupInfo.emailCode} />
                                        </form>
                                    </div>
                                </section>
                                <S.NextButtonBlock isPaint={isPaint} onClick={onSubmit} >
                                    {
                                        isPaint === true ?
                                            <div>
                                                <img alt="" src={checkIcon} />
                                                <p>  .</p>
                                            </div>
                                        : <h1>Next</h1>
                                    }
                                </S.NextButtonBlock>
                            </>
                            : ''
                        }
                        {
                            page === 2 ?
                            <>
                                <section>
                                    <div>
                                        <div>
                                            <span> </span>
                                            <S.LOGINSIGNUPInput placeholder="    ." name="personalCode" onChange={onChange} value={signupInfo.personalCode}  />
                                        </div>
                                        <div>
                                            <span></span>
                                            <S.LOGINSIGNUPInput type="password" placeholder="6  12 ,    ." name="password" onChange={onChange} value={signupInfo.password} />
                                        </div>
                                        <form onSubmit={onSubmit}>
                                            <span> </span>
                                            <S.LOGINSIGNUPInput type="password" placeholder="  ." name="chkPassword" onChange={onChange} value={signupInfo.chkPassword} />
                                        </form>
                                    </div>
                                </section>
                                <S.NextButtonBlock onClick={onSubmit}>
                                    <h1></h1>
                                </S.NextButtonBlock> 
                            </>
                            : ''
                        }
                    </S.MainWrapper>
                    <h4>2020 SCIENCE CLASS</h4>
                </S.SignUpRightBlock>
            </S.LOGINSIGNUPWrapper>
        </S.ModalBackground>
    );
};

export default SignUp;