import React from 'react';
import { HomeWorkItemWrapper } from './styles';

const HomeWorkItem = ({ data }) => {
    // homeworkType: 0 1 2
    return (
        <HomeWorkItemWrapper submit={data.submissionStatus}>
            <main>
                <h1>{data.homeworkTitle}</h1>
                <div>
                    <p>{new Date(data['created_at']).yyyymmddWithDot()} - {new Date(data['homework_deadline']).yyyymmddWithDot()}</p>
                    <span>{Date.prototype.getDifferenceDate(data['homework_deadline'], data['created_at'])}</span>
                </div>
            </main>
            <footer>
                <span>[{data.homeworkType === 0 ? '' : data.homeworkType === 1 ? '' : ''}]</span>
                <div><span>{data.submissionStatus ? '' : ' '}</span></div>
            </footer>
        </HomeWorkItemWrapper>           
    );
};

export default HomeWorkItem;